<template>
  <div class="Edit-container">
    <van-nav-bar
      class="page-nav-bar nav-bar"
      left-arrow
      title=""
      @click-left="$router.back()"
    ></van-nav-bar>
    <van-cell title="" class="avatar-cell" is-link @click="$refs.filePhoto.click()">
      <template #default>
        <van-image class="avatar" :src="userInfo.photo"></van-image>
      </template>
    </van-cell>
    <van-cell title="" is-link @click="birthdayShow=true">
      <template #default>
        {{ userInfo.birthday }}
      </template>
    </van-cell>
    <input type="file" hidden ref="filePhoto" @change="fileInput">
    <van-popup
      v-model="changeAvatarShow"
      position="bottom"
      :style="{ height: '100%' }"
      @closed="onCloseId=Math.random()"
    >
      <CopporAvatar

        :avatarUrl="avatarUrl"
        :changeAvatarShow="changeAvatarShow"
        :onClose="onCloseId"
        @cancelProp="changeAvatarShow=false"
        @newAvatar="newAvatar"

      ></CopporAvatar>
    </van-popup>
    <van-popup v-model="birthdayShow" position="bottom" :style="{ height: '40%' }">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="commitBirthday"
        @cancel="birthdayShow=false"
      />
    </van-popup>
  </div>
</template>

<script>
import getProfile from "../../api/user/getProfile";
import CopporAvatar from "./component/CopporAvatar";

export default {
  name: "Edit",
  components: {CopporAvatar},
  props: [],
  data() {
    return {
      userInfo: {},
      changeAvatarShow: false,
      birthdayShow:false,
      avatarUrl: '',
      onCloseId: 1,
      minDate: new Date(1900, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),

    }
  },
  methods: {
    commitBirthday(time) {
      // console.log(time)
    },
    newAvatar(url) {
      this.userInfo.photo = url;
    },
    async loadProfile() {
      try {
        let {data: {data}} = await getProfile();
        this.userInfo = data;
        this.currentDate = new Date(data.birthday)
      } catch (e) {

      }
    },
    fileInput(e) {
      try {
        const file = e.target.files[0];
        this.avatarUrl = URL.createObjectURL(file);
        this.changeAvatarShow = true;
      } catch (e) {
        // console.log('sss');
        // this.$toast('')
      }

    }
  },
  computed: {},
  watch: {},
  created() {
    this.loadProfile();

  },
  mounted() {
    this.userInfo = this.$route.query;
    // console.log()
  }
}
</script>

<style scoped lang="less">
.avatar-cell {
  display: flex;
  align-items: center;
}

.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
}
</style>
