<template>
  <div class="CopporAvatar-container f-r">
    <img :src="avatarUrl" alt="" ref="img">
    <div class="footer f-r j-b">
      <div class="btn f-r" @click="$emit('cancelProp')"></div>
      <div class="btn f-r" @click="uploadAvatar"></div>
    </div>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css';
import Cropper from 'cropperjs';
import patchAvatar from "../../../api/user/patchAvatar";

export default {
  name: "CrooporAvatar",
  components: {},
  props: ['avatarUrl', 'changeAvatarShow', 'onClose'],
  data() {
    return {
      cropper: null
    }
  },
  methods: {

    uploadAvatar() {
      this.cropper.getCroppedCanvas().toBlob(async (blob) => {
        const formData = new FormData();
        // Pass the image file name as the third parameter if necessary.
        formData.append('photo', blob/*, 'example.png' */);
        try {
          // patchAvatar
          this.$toast.loading('...');
          let {data: {data}} = await patchAvatar(formData);
          this.$emit('cancelProp');
          this.$emit('newAvatar', data.photo);
          this.$toast.success('');
          console.log(data);
        } catch (e) {
          this.$toast.fail('');
        }

      }/*, 'image/png' */);
    },
    initCopper() {
      const image = this.$refs.img;
      this.cropper = new Cropper(image, {
        aspectRatio: 1,
        viewMode: 1,
        dragMode: 'move',
        background: false,
        crop(event) {
          /*       console.log(event.detail.x);
                 console.log(event.detail.y);
                 console.log(event.detail.width);
                 console.log(event.detail.height);
                 console.log(event.detail.rotate);
                 console.log(event.detail.scaleX);
                 console.log(event.detail.scaleY);*/
        },
      });
    }
  },
  computed: {},
  watch: {
    avatarUrl() {
      this.$nextTick(this.initCopper);
    },
    onClose() {
      this.cropper.destroy();
    },
  },
  created() {
  },
  mounted() {
    this.initCopper();

  }
}
</script>

<style scoped lang="less">
.CopporAvatar-container {
  background: #000;
  height: 100vh;

  .footer {

    position: absolute;
    width: 100%;
    bottom: 0;
    color: white;
  }

  .btn {
    width: 160px;
    height: 100px;
    font-size: 38px;
    font-weight: bold;
  }
}

img {
  max-width: 100%;
}
</style>
